




import { Component, Prop, Vue } from "vue-property-decorator";

@Component
export default class CommonLoader extends Vue {
  @Prop({ default: 10 }) size!: number;
  @Prop({ default: 60 }) margin!: number;

  get styleVars(): any {
    return {
      "--loader-size": `${this.size}em`,
      "--margin": `${this.margin}px`
    };
  }
}
